import './App.css';
const {addComment} = require('./notion');

function App() {
  return (
    <div className="App">
      <header className="header">
      <div className='menu'>1</div>
      <div className='menu'>2</div>
      <div className='menu'>3</div>
      <div className='menu'>4</div>
      </header>
      <div className='snow-bg'></div><br></br>
      <div className='body'>
        <div className='box'></div>
        <div className='comment'>
          <button onClick={addComment('','','1234')}></button>
          
        </div>
      </div>
    </div>
  );
}

export default App;
